import React, { useState } from 'react';
import './SimplePages.css';

const dummyData = [
  { id: "AR-001", department: "DTD", title: "Tax Filing System", status: "Fully Implemented", deadline: "2025-12-31", progress: 100 },
  { id: "AR-002", department: "CSD", title: "Customs Clearance", status: "Partially Implemented", deadline: "2026-01-31", progress: 65 },
  { id: "AR-003", department: "FIND", title: "Financial Controls", status: "In Progress", deadline: "2026-02-28", progress: 45 },
  { id: "AR-004", department: "ITDTD", title: "System Security", status: "Not Implemented", deadline: "2025-11-30", progress: 0 },
  { id: "AR-005", department: "HR", title: "Payroll Reconciliation", status: "Fully Implemented", deadline: "2025-12-15", progress: 100 },
  { id: "AR-006", department: "DTD", title: "Audit Trail", status: "Beyond Control", deadline: "2026-03-31", progress: 30 },
];

const statusColors = {
  "Fully Implemented": { bg: "#e8f5e9", color: "#2e7d32" },
  "Partially Implemented": { bg: "#fff3e0", color: "#e65100" },
  "In Progress": { bg: "#e3f2fd", color: "#1565c0" },
  "Not Implemented": { bg: "#ffebee", color: "#c62828" },
  "Beyond Control": { bg: "#f3e5f5", color: "#6a1b9a" },
};

export default function DepartmentDashboard() {
  const [page, setPage] = useState(0);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterDept, setFilterDept] = useState('');

  const filteredData = dummyData.filter((item) => {
    const matchesSearch = item.id.includes(searchTerm) || 
                          item.title.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesDept = !filterDept || item.department === filterDept;
    return matchesSearch && matchesDept;
  });

  const departments = [...new Set(dummyData.map(d => d.department))];
  const rowsPerPage = 5;
  const paginatedData = filteredData.slice(page * rowsPerPage, (page + 1) * rowsPerPage);

  return (
    <div className="simple-page">
      <div className="page-header">
        <h1> Department Dashboard</h1>
        <p>Track and manage recommendations by department</p>
      </div>

      {/* Filter Section */}
      <div style={{ display: 'flex', gap: '12px', marginBottom: '20px', flexWrap: 'wrap' }}>
        <input
          type="text"
          placeholder="Search by ID or title..."
          value={searchTerm}
          onChange={(e) => { setSearchTerm(e.target.value); setPage(0); }}
          style={{ flex: 1, minWidth: '200px', padding: '8px 12px', border: '1px solid #ddd', borderRadius: '4px' }}
        />
        <select value={filterDept} onChange={(e) => { setFilterDept(e.target.value); setPage(0); }} style={{ padding: '8px 12px', border: '1px solid #ddd', borderRadius: '4px', minWidth: '150px' }}>
          <option value="">All Departments</option>
          {departments.map(dept => <option key={dept}>{dept}</option>)}
        </select>
        <button className="btn btn-primary" style={{ padding: '8px 16px' }}> Export</button>
      </div>

      {/* Data Table */}
      <div style={{ overflowX: 'auto', marginBottom: '20px' }}>
        <table style={{ width: '100%', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ backgroundColor: '#f5f5f5', borderBottom: '2px solid #ddd' }}>
              <th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>ID</th>
              <th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Department</th>
              <th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Title</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Status</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Progress</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Deadline</th>
            </tr>
          </thead>
          <tbody>
            {paginatedData.map((row) => (
              <tr key={row.id} style={{ borderBottom: '1px solid #e0e0e0' }}>
                <td style={{ padding: '12px', fontSize: '13px', fontWeight: 'bold', color: '#1976d2' }}>{row.id}</td>
                <td style={{ padding: '12px', fontSize: '13px', color: '#333' }}>{row.department}</td>
                <td style={{ padding: '12px', fontSize: '13px', color: '#555' }}>{row.title}</td>
                <td style={{ padding: '12px', textAlign: 'center' }}>
                  <span style={{ padding: '4px 8px', borderRadius: '4px', fontSize: '11px', fontWeight: '600', ...statusColors[row.status] }}>
                    {row.status}
                  </span>
                </td>
                <td style={{ padding: '12px', textAlign: 'center', fontSize: '12px' }}>
                  <div style={{ display: 'flex', alignItems: 'center', gap: '8px', justifyContent: 'center' }}>
                    <div style={{ width: '60px', height: '6px', backgroundColor: '#e0e0e0', borderRadius: '3px', overflow: 'hidden' }}>
                      <div style={{ width: `${row.progress}%`, height: '100%', backgroundColor: row.progress === 100 ? '#4caf50' : '#ff9800' }} />
                    </div>
                    <span>{row.progress}%</span>
                  </div>
                </td>
                <td style={{ padding: '12px', textAlign: 'center', fontSize: '13px', color: '#555' }}>{new Date(row.deadline).toLocaleDateString()}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Pagination */}
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', fontSize: '13px' }}>
        <span>Showing {page * rowsPerPage + 1}-{Math.min((page + 1) * rowsPerPage, filteredData.length)} of {filteredData.length}</span>
        <div style={{ display: 'flex', gap: '8px' }}>
          <button
            className="btn btn-secondary"
            onClick={() => setPage(Math.max(0, page - 1))}
            disabled={page === 0}
            style={{ padding: '6px 12px', fontSize: '12px' }}
          >
             Previous
          </button>
          <span style={{ padding: '6px 12px' }}>Page {page + 1}</span>
          <button
            className="btn btn-secondary"
            onClick={() => setPage(page + 1)}
            disabled={(page + 1) * rowsPerPage >= filteredData.length}
            style={{ padding: '6px 12px', fontSize: '12px' }}
          >
            Next 
          </button>
        </div>
      </div>
    </div>
  );
}