import React, { useState } from 'react';
import './SimplePages.css';

const departments = [
  "CG Office", "DTD", "CSD", "FIND", "ITDTD", "LSBAD", "SRAD",
  "IAID", "ALD", "HR", "SIID", "TPS", "TAD", "PDRD", "DMD",
  "RFPD", "TCOSD", "COD", "COSD", "SPIU", "CRADAD", "PRSD", "RAD"
];

const auditTypes = ["Internal Audit (IA)", "External Audit (EA)", "QMS Internal", "QMS External"];
const statuses = ["Not Started", "In Progress", "Pending Review", "Implemented", "Closed"];

export default function AuditRecommendationEntry() {
  const [activeStep, setActiveStep] = useState(0);
  const [form, setForm] = useState({
    team: "", auditType: "", reportTitle: "", scope: "",
    findingTitle: "", description: "", recommendation: "", actions: "",
    timeline: "", responsibleDepartment: "", responsibleStaff: "",
    status: "Not Started", auditor: ""
  });

  const steps = ['Audit Info', 'Details', 'Assignment', 'Review'];

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    alert('Recommendation submitted successfully!');
  };

  return (
    <div className="simple-page">
      <div className="page-header">
        <h1> Add New Recommendation</h1>
        <p>Enter audit findings and recommendations details</p>
      </div>

      {/* Stepper */}
      <div className="stepper">
        {steps.map((step, idx) => (
          <div key={idx} className={`stepper-item ${idx === activeStep ? 'active' : ''} ${idx < activeStep ? 'completed' : ''}`}>
            <div className="stepper-number">{idx < activeStep ? '' : idx + 1}</div>
            <div className="stepper-label">{step}</div>
          </div>
        ))}
      </div>

      <form onSubmit={handleSubmit}>
        <div className="form-container">
          {/* Step 1: Audit Info */}
          {activeStep === 0 && (
            <div className="form-section">
              <h3>Step 1: Audit Information</h3>
              <div className="form-group">
                <label>Team/Department</label>
                <input type="text" name="team" value={form.team} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Audit Type</label>
                <select name="auditType" value={form.auditType} onChange={handleChange} required>
                  <option value="">Select...</option>
                  {auditTypes.map(t => <option key={t}>{t}</option>)}
                </select>
              </div>
              <div className="form-group">
                <label>Report Title</label>
                <input type="text" name="reportTitle" value={form.reportTitle} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Scope</label>
                <textarea name="scope" value={form.scope} onChange={handleChange} />
              </div>
            </div>
          )}

          {/* Step 2: Details */}
          {activeStep === 1 && (
            <div className="form-section">
              <h3>Step 2: Recommendation Details</h3>
              <div className="form-group">
                <label>Finding Title</label>
                <input type="text" name="findingTitle" value={form.findingTitle} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Description</label>
                <textarea name="description" value={form.description} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Recommendation</label>
                <textarea name="recommendation" value={form.recommendation} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Corrective Actions</label>
                <textarea name="actions" value={form.actions} onChange={handleChange} />
              </div>
            </div>
          )}

          {/* Step 3: Assignment */}
          {activeStep === 2 && (
            <div className="form-section">
              <h3>Step 3: Assignment</h3>
              <div className="form-group">
                <label>Responsible Department</label>
                <select name="responsibleDepartment" value={form.responsibleDepartment} onChange={handleChange} required>
                  <option value="">Select...</option>
                  {departments.map(d => <option key={d}>{d}</option>)}
                </select>
              </div>
              <div className="form-group">
                <label>Responsible Staff</label>
                <input type="text" name="responsibleStaff" value={form.responsibleStaff} onChange={handleChange} />
              </div>
              <div className="form-group">
                <label>Implementation Timeline</label>
                <input type="date" name="timeline" value={form.timeline} onChange={handleChange} required />
              </div>
              <div className="form-group">
                <label>Status</label>
                <select name="status" value={form.status} onChange={handleChange}>
                  {statuses.map(s => <option key={s}>{s}</option>)}
                </select>
              </div>
              <div className="form-group">
                <label>Auditor Name</label>
                <input type="text" name="auditor" value={form.auditor} onChange={handleChange} required />
              </div>
            </div>
          )}

          {/* Step 4: Review */}
          {activeStep === 3 && (
            <div className="form-section">
              <h3>Step 4: Review Summary</h3>
              <div style={{ backgroundColor: '#fff3cd', padding: '12px', borderRadius: '4px', marginBottom: '16px', fontSize: '13px' }}>
                Please review all details before submitting
              </div>
              <div style={{ fontSize: '13px', lineHeight: '1.8' }}>
                <p><strong>Team:</strong> {form.team}</p>
                <p><strong>Audit Type:</strong> {form.auditType}</p>
                <p><strong>Report:</strong> {form.reportTitle}</p>
                <p><strong>Department:</strong> {form.responsibleDepartment}</p>
                <p><strong>Status:</strong> {form.status}</p>
              </div>
            </div>
          )}

          {/* Navigation */}
          <div className="form-buttons">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={() => setActiveStep(Math.max(0, activeStep - 1))}
              disabled={activeStep === 0}
            >
              Back
            </button>
            {activeStep === steps.length - 1 ? (
              <button type="submit" className="btn btn-success">
                 Submit Recommendation
              </button>
            ) : (
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => setActiveStep(activeStep + 1)}
              >
                Next 
              </button>
            )}
          </div>
        </div>
      </form>
    </div>
  );
}
