import React, { useState } from 'react';
import './SimplePages.css';

const dummyUsers = [
  { id: 1, name: 'Jane Auditor', email: 'jane.auditor@rra.rw', role: 'Auditor', department: 'IAID', status: 'Active' },
  { id: 2, name: 'John Director', email: 'john.director@rra.rw', role: 'Director', department: 'IAID', status: 'Active' },
  { id: 3, name: 'Mary Finance', email: 'mary.finance@rra.rw', role: 'Department Focal', department: 'FIND', status: 'Active' },
  { id: 4, name: 'Peter IT', email: 'peter.it@rra.rw', role: 'Department Staff', department: 'ITDTD', status: 'Inactive' },
  { id: 5, name: 'Sarah Tax', email: 'sarah.tax@rra.rw', role: 'Department Staff', department: 'DTD', status: 'Active' },
];

const roles = ['Auditor', 'Director', 'Department Focal', 'Department Staff', 'Admin'];
const departments = ['IAID', 'DTD', 'CSD', 'FIND', 'ITDTD', 'HR', 'Operations'];

const roleColors = {
  'Auditor': '#1976d2',
  'Director': '#f44336',
  'Department Focal': '#4caf50',
  'Department Staff': '#ff9800',
  'Admin': '#9c27b0',
};

export default function UserManagement() {
  const [page, setPage] = useState(0);
  const [openDialog, setOpenDialog] = useState(false);
  const [editingUser, setEditingUser] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    role: 'Department Staff',
    department: '',
    status: 'Active',
  });

  const rowsPerPage = 5;
  const paginatedUsers = dummyUsers.slice(page * rowsPerPage, (page + 1) * rowsPerPage);

  const handleOpenDialog = (user = null) => {
    if (user) {
      setFormData(user);
      setEditingUser(user);
    } else {
      setFormData({
        name: '',
        email: '',
        role: 'Department Staff',
        department: '',
        status: 'Active',
      });
      setEditingUser(null);
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  const handleSaveUser = () => {
    alert(editingUser ? 'User updated successfully!' : 'User created successfully!');
    handleCloseDialog();
  };

  const handleChangeForm = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <div className="simple-page">
      <div className="page-header">
        <h1> User Management</h1>
        <p>Manage system users, roles, and permissions</p>
      </div>

      {/* Action Buttons */}
      <div style={{ display: 'flex', gap: '12px', marginBottom: '20px', flexWrap: 'wrap' }}>
        <button className="btn btn-success" onClick={() => handleOpenDialog()} style={{ padding: '8px 16px' }}>
           Add User
        </button>
        <button className="btn btn-secondary" style={{ padding: '8px 16px' }}>
           Reset Password
        </button>
      </div>

      {/* Users Table */}
      <div style={{ overflowX: 'auto', marginBottom: '20px' }}>
        <table style={{ width: '100%', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ backgroundColor: '#f5f5f5', borderBottom: '2px solid #ddd' }}>
              <th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Name</th>
              <th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Email</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Role</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Department</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Status</th>
              <th style={{ padding: '12px', textAlign: 'center', fontSize: '13px', fontWeight: '600', color: '#1976d2' }}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedUsers.map(user => (
              <tr key={user.id} style={{ borderBottom: '1px solid #e0e0e0' }}>
                <td style={{ padding: '12px', fontSize: '13px', fontWeight: 'bold' }}>{user.name}</td>
                <td style={{ padding: '12px', fontSize: '13px', color: '#555' }}>{user.email}</td>
                <td style={{ padding: '12px', textAlign: 'center' }}>
                  <span style={{ padding: '4px 8px', borderRadius: '4px', fontSize: '11px', fontWeight: '600', backgroundColor: roleColors[user.role], color: '#fff' }}>
                    {user.role}
                  </span>
                </td>
                <td style={{ padding: '12px', textAlign: 'center', fontSize: '13px', color: '#555' }}>{user.department}</td>
                <td style={{ padding: '12px', textAlign: 'center' }}>
                  <span style={{ padding: '4px 8px', borderRadius: '4px', fontSize: '11px', fontWeight: '600', color: user.status === 'Active' ? '#4caf50' : '#999', border: `1px solid ${user.status === 'Active' ? '#4caf50' : '#ccc'}` }}>
                    {user.status}
                  </span>
                </td>
                <td style={{ padding: '12px', textAlign: 'center', display: 'flex', gap: '8px', justifyContent: 'center' }}>
                  <button className="btn btn-secondary" onClick={() => handleOpenDialog(user)} style={{ padding: '4px 8px', fontSize: '11px' }}> Edit</button>
                  <button className="btn btn-secondary" style={{ padding: '4px 8px', fontSize: '11px' }}> Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Pagination */}
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', fontSize: '13px' }}>
        <span>Showing {page * rowsPerPage + 1}-{Math.min((page + 1) * rowsPerPage, dummyUsers.length)} of {dummyUsers.length}</span>
        <div style={{ display: 'flex', gap: '8px' }}>
          <button
            className="btn btn-secondary"
            onClick={() => setPage(Math.max(0, page - 1))}
            disabled={page === 0}
            style={{ padding: '6px 12px', fontSize: '12px' }}
          >
             Previous
          </button>
          <span style={{ padding: '6px 12px' }}>Page {page + 1}</span>
          <button
            className="btn btn-secondary"
            onClick={() => setPage(page + 1)}
            disabled={(page + 1) * rowsPerPage >= dummyUsers.length}
            style={{ padding: '6px 12px', fontSize: '12px' }}
          >
            Next 
          </button>
        </div>
      </div>

      {/* Add/Edit User Dialog */}
      {openDialog && (
        <div style={{ position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: 'rgba(0,0,0,0.5)', display: 'flex', alignItems: 'center', justifyContent: 'center', zIndex: 1000 }}>
          <div style={{ backgroundColor: '#fff', borderRadius: '8px', padding: '20px', maxWidth: '500px', width: '90%', maxHeight: '90vh', overflowY: 'auto' }}>
            <h2 style={{ margin: '0 0 16px 0', color: '#1976d2' }}>{editingUser ? 'Edit User' : 'Add New User'}</h2>
            <div className="form-container" style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
              <div className="form-group">
                <label>Full Name</label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleChangeForm}
                />
              </div>
              <div className="form-group">
                <label>Email</label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChangeForm}
                />
              </div>
              <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px' }}>
                <div className="form-group" style={{ margin: 0 }}>
                  <label>Role</label>
                  <select name="role" value={formData.role} onChange={handleChangeForm}>
                    {roles.map(r => <option key={r}>{r}</option>)}
                  </select>
                </div>
                <div className="form-group" style={{ margin: 0 }}>
                  <label>Department</label>
                  <select name="department" value={formData.department} onChange={handleChangeForm}>
                    <option value="">Select...</option>
                    {departments.map(d => <option key={d}>{d}</option>)}
                  </select>
                </div>
              </div>
              <div className="form-group">
                <label>Status</label>
                <select name="status" value={formData.status} onChange={handleChangeForm}>
                  <option>Active</option>
                  <option>Inactive</option>
                </select>
              </div>
              <div style={{ display: 'flex', gap: '12px', justifyContent: 'flex-end', marginTop: '12px' }}>
                <button className="btn btn-secondary" onClick={handleCloseDialog} style={{ padding: '8px 16px' }}>Cancel</button>
                <button className="btn btn-success" onClick={handleSaveUser} style={{ padding: '8px 16px' }}>
                  {editingUser ? ' Update' : ' Save'}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}